import React from "react";
import { Button } from "reactstrap";
import "bootstrap/dist/css/bootstrap.css";
import "../../static/foundation-6.5.1-custom/css/foundation.min.css";

export default class SearchForm extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div className="grid-container">
        <div className="grid-x">
          <div className="small-12 medium-6 large-3 cellio">
            <Button color="danger">Danger!</Button>
          </div>
          <div className="small-12 medium-6 large-3 cellio">Arrive</div>
          <div className="small-12 medium-6 large-3 cellio">Date</div>
          <div className="small-12 medium-6 large-3 cellio">Search</div>
        </div>
        <style jsx>{`
          .cellio {
            height: 100px;
            background-color: dimgrey;
            padding: 5px;
          }
        `}</style>
      </div>
    );
  }
}
